import { Message, WelcomeSectionData } from "../typescript/types";
import { Input } from "antd";
import EngagedAi from "../assets/icons/svg/EngagedAiIcon.svg";
import SendIcon from "../assets/icons/jsx/SendIcon";
import { useEffect, useState } from "react";
import { isEmpty } from "lodash";
import dayjs from "dayjs";
import { MessageJSX } from "./MessageJSX";

export function MessageSection(props: WelcomeSectionData) {
  const { companyInformation, setStepData, stepData } = props;
  console.log(
    "🚀 ~ ChatSection ~ companyInformation, setStepData, stepData:",
    companyInformation,
    setStepData,
    stepData
  );

  const [messages, setmessages] = useState<Message[]>([]);
  console.log(messages);
  useEffect(() => {
    if (isEmpty(stepData.question)) {
      setmessages([
        ...messages,
        {
          message: companyInformation.initialText!,
          date: dayjs().toISOString(),
          direction: "OUTGOING",
        },
      ]);
    } else {
      setmessages([
        ...messages,
        {
          message: stepData.question,
          date: dayjs().toISOString(),
          direction: "OUTGOING",
        },
      ]);
    }
  }, [stepData.question]);

  return (
    <div>
      {/* Header */}
      <div className="header flex justify-start items-center pl-3 h-[65px] rounded-t-[12px]">
        <div className="flex flex-row items-center gap-2">
          <span className="bg-white p-1 rounded-full">
            <img src={EngagedAi} />
          </span>
          <span className="text-white font-[600] text-[16px]">Engaged Ai</span>
        </div>
      </div>
      {/* Message Content Box */}
      <div className="home-gradient h-[495px] p-2">
        {messages.map((item, index) => {
          return <MessageJSX index={index} item={item} />;
        })}
      </div>
      {/* Footer Input Box */}
      <div className="top-auto relative">
        <Input.TextArea
          rows={1}
          className="rounded-b-[12px] p-2 w-full resize-none min-h-[25px] border-t-[1px] "
          placeholder="Type your Message"
        />
        <SendIcon className="absolute top-2.5 right-2.5" />
      </div>
    </div>
  );
}
