import React from "react";
import ReactDOM from "react-dom/client";
import App from "./App";
import "./index.css";
// import "antd/dist/antd.min.css";
import { ApolloClient, ApolloProvider, InMemoryCache } from "@apollo/client";

const widget = document.createElement("div");
widget.id = "chatbot-widget";
widget.style.position = "relative";
widget.style.right = "0px";
widget.style.bottom = "0px";
document.body.appendChild(widget);
const getId = document.getElementById("business-id");
console.log(process.env.REACT_APP_GRAPHQL_URL);
const client = new ApolloClient({
  uri: process.env.REACT_APP_GRAPHQL_URL || "http://localhost:5000/chat_api",
  cache: new InMemoryCache(),
});
if (getId) {
  ReactDOM.createRoot(document.getElementById("chatbot-widget")!).render(
    <ApolloProvider client={client}>
      <React.StrictMode>
        <App id={getId!.getAttribute("ng-Uid")} />
      </React.StrictMode>
    </ApolloProvider>
  );
}
