import { useState } from "react";
import "./App.css";
import DownIconJsx from "./assets/icons/jsx/DownIcon";
import EngagedAiIcon from "./assets/icons/jsx/EngagedAiIcon";
import { useQuery } from "@apollo/client";
import { GET_UNIQUE_ID_DETAILS } from "./graphql/query";
import { ChatWidgetInformation } from "./typescript/types";
import { ChatContent } from "./Chat/ChatContent";

type AppProps = {
  id: string | null;
};

function App(props: AppProps) {
  const [widgetVisible, setwidgetVisible] = useState<boolean>(false);
  const [companyInformation, setcompanyInformation] = useState<
    Partial<ChatWidgetInformation>
  >({});
  useQuery(GET_UNIQUE_ID_DETAILS, {
    fetchPolicy: "network-only",
    variables: {
      widgetUid: props?.id?.replace(/\s+/g, " ")?.trim(),
    },
    onCompleted: (res: { getWebChatWidget: ChatWidgetInformation }) => {
      setcompanyInformation(res.getWebChatWidget);
    },
  });
  return (
    <div style={{ height: "100%" }}>
      {/* <div
        style={{
          position: 'fixed',
          bottom: 0,
          marginBottom: '65px',
          right: 0,
          marginRight: '50px',
        }}
      ></div> */}
      <div
        style={{
          position: "fixed",
          bottom: 0,
          marginBottom: "30px",
          right: 0,
          marginRight: "50px",
        }}
      >
        <div
          style={{
            display: `${widgetVisible ? "block" : "none"}`,
            marginLeft: "auto",
            marginRight: "16px",
            marginBottom: "8px",
            borderRadius: "15px",
            // border: "1px solid",
            borderBottomRightRadius: "0px",
            zIndex: 9999,
            backgroundColor: "white",
          }}
        >
          {/* <WidgetContent id={props.id} /> */}
          <ChatContent companyInformation={companyInformation} />
        </div>
        <div className="w-full flex justify-end">
          <span
            className="cursor-pointer bg-white p-2 rounded-full shadow-md w-max"
            onClick={() => setwidgetVisible((prev) => !prev)}
          >
            {widgetVisible ? <DownIconJsx /> : <EngagedAiIcon />}
          </span>
        </div>
      </div>
      {/* </Popover> */}
    </div>
  );
}

export default App;
