import { Message } from "../typescript/types";

export const MessageJSX = (props: { item: Message; index: number }) => {
  const { index, item } = props;
  return (
    <div
      key={index}
      className={`w-full flex ${
        item.direction === "OUTGOING" ? "justify-end" : "justify-start"
      } `}
    >
      <span className={`${item.direction === "OUTGOING" ? "text-sendTextColor bg-SendBGColor rounded-[8px] rounded-tr-none" : "text-recieveTextColor bg-recieveBGColor"} p-2 w-max max-w-[320px]`}>
        {item.message}
      </span>
    </div>
  );
};
