/* eslint-disable @typescript-eslint/no-unused-vars */
import { useState } from "react";
import AirplaneIcon from "../assets/icons/jsx/AirplaneIcon";
// import EngagedAiIcon from "./assets/icons/jsx/EngagedAiIcon";
import MessageOutlinedIcon from "../assets/icons/jsx/MessageOutlined";
import {
  ChatWidgetInformation,
  StepData,
  WelcomeSectionData,
} from "../typescript/types";
import { Button, Radio } from "antd";
import EngagedAi from "../assets/icons/svg/EngagedAiIcon.svg";
import { MessageSection } from "./MessageSection";
export function ChatContent(props: {
  companyInformation: Partial<ChatWidgetInformation>;
}) {
  const [stepData, setStepData] = useState<StepData>({
    step: 0,
    question: "",
  });
  const ConditionalRenderComponent = () => {
    switch (stepData.step) {
      case 0:
        return (
          <WelcomeSection
            companyInformation={props.companyInformation}
            setStepData={setStepData}
            stepData={stepData}
          />
        );

      case 1:
        return (
          <MessageSection
            companyInformation={props.companyInformation}
            setStepData={setStepData}
            stepData={stepData}
          />
        );
      default:
        return <></>;
    }
  };
  return (
    <div className=" w-[400px] border-solid border-[1px] h-[600px] rounded-[12px]">
      <ConditionalRenderComponent />
    </div>
  );
}

function WelcomeSection(props: WelcomeSectionData) {
  const { companyInformation, setStepData, stepData } = props;
  return (
    <>
      <div className="h-1/2 w-full p-4 pb-0 home-gradient flex flex-col justify-between rounded-t-[12px]">
        <div className="flex flex-row items-center justify-between">
          <span className="text-[18px] font-[700] text-white font-sans">
            Welcome to {props.companyInformation?.location?.title}
          </span>
          <span className="bg-white shadow-md p-2 rounded-full">
            <img src={EngagedAi} />
            {/* <EngagedAi /> */}
          </span>
        </div>
        <div className="flex flex-col items-start">
          <span className=" text-[#2563EB] font-[700] text-[24px]">
            Hi there 👋
          </span>
          <span className="font-[700] text-[24px]">How can we help?</span>
        </div>
      </div>
      <div className="h-1/2 w-full flex flex-col justify-between p-3 pt-0 bg-white rounded-b-[12px]">
        <div className="flex flex-wrap gap-2">
          {companyInformation.webChatWidgetQuestion?.map((question, index) => {
            return (
              <Button
                key={index}
                onClick={() => {
                  setStepData({
                    ...stepData,
                    step: 1,
                    question: question.question,
                  });
                }}
                className="border-[1px] rounded-[6px]"
              >
                {question.question}
              </Button>
            );
          })}
        </div>
        <div className="w-full h-max">
          <Radio.Group className="w-full h-max flex flex-row items-center ">
            <Radio.Button
              onClick={() =>
                setStepData({
                  ...stepData,
                  step: 1,
                  question: "",
                })
              }
              className="w-1/2 rounded-l-[12px] h-[72px] p-2 more-options-radio"
            >
              <div className="flex flex-col items-center">
                <MessageOutlinedIcon />
                Chat with us
              </div>
            </Radio.Button>
            <Radio.Button className="w-1/2 rounded-r-[12px] h-[72px] p-2 more-options-radio">
              <div className="flex flex-col items-center">
                <AirplaneIcon />
                SMS us
              </div>
            </Radio.Button>
          </Radio.Group>
        </div>
      </div>
    </>
  );
}
